body.modal-open {
  overflow: hidden;
}

.backdrop {
  z-index: 160;
  width: 100%;
  height: 100%;
  background-color: var(--background-mobile-menu-color);
  opacity: 1;
  transition: opacity var(--time-duration) var(--timing-function);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.backdrop.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.backdrop.is-hidden .modal {
  z-index: 160;
  transform: translate(-50%, -100%)scale(.7);
}

